.home{
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    &__social{

        padding: 20px;
        text-align: center;
        display: flex;
        
        &__icons{
            color: var(--main-theme-main-color);
            cursor: pointer;
            padding: 20px;
        }
    }

    &__text-wrapper{
        display: flex;
        justify-content: center;


        h1{
            color: var(--main-theme-main-color);
            margin: 0px;
            font-weight: 500;
            font-size: 6rem;
            line-height: 8rem;
            position: relative;
            overflow: hidden;
            white-space: nowrap;
            animation: typingEffectAnimation 2s steps(30,end);
            text-align: center;
        }
    }

    &__contact-me{
        display: flex;
        justify-content: center;
        margin-top: 15px;

        button{
            cursor: pointer;
            padding: 10px 65px;
            font-size: 2rem;
            background: transparent;
            color: var(--main-theme-main-color);
            border: 1.5px solid var(--main-theme-main-color);
            border-radius: 4px;
            font-weight: 500;

            &:hover{
                background: var(--main-theme-main-color);
                color: var(--main-theme-background-color);
            }
        }
    }
}

@media only screen and (max-width: 350px){
    .home__text-wrapper h1{
        font-size: 2rem;
        line-height: 3rem;
    }
}

@media only screen and (min-width : 351px) and (max-width: 550px){
    .home__text-wrapper h1{
        font-size: 3rem;
        line-height: 5rem;
    }
}

@media only screen and (min-width : 551px) and (max-width: 768px){
    .home__text-wrapper h1{
        font-size: 4rem;
        line-height: 6rem;
    }
}

@media only screen and (min-width : 769px) and (max-width: 990px){
    .home__text-wrapper h1{
        font-size: 6rem;
        line-height: 8rem;
    }
}

@keyframes typingEffectAnimation{
    from{
        width: 0;
    }

    to{
        width: 100%;
    }
}